.GalleryItem {
  width: calc((100% - 15px) / 5);
  padding: 30px 50px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}
.GearRight {
  margin-left: -15px;
  transform: rotate(12deg);
}

.GalleryItem:hover .GearRight,
.GalleryItem:focus .GearRight {
  animation: rotating 2s linear infinite;
}

.GalleryItem:hover .GearLeft,
.GalleryItem:focus .GearLeft {
  animation: rotatingReverse 2s linear infinite;
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotatingReverse {
  from {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  to {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}
.ItemContainer {
  display: flex;
  justify-content: center;
  gap: 0px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}

.GalleryItem:hover .Lightening,
.GalleryItem:focus .Lightening {
  animation: lighteningTranslate 1s ease-in-out infinite;
}
@keyframes lighteningTranslate {
  from {
    -ms-transform: translateY(-110px);
    -moz-transform: translateY(-110px);
    -webkit-transform: translateY(-110px);
    -o-transform: translateY(-110px);
    transform: translateY(-110px);
  }
  to {
    -ms-transform: translateY(110px);
    -moz-transform: translateY(110px);
    -webkit-transform: translateY(110px);
    -o-transform: translateY(110px);
    transform: translateY(110px);
  }
}

.GalleryItem:hover .HandPick,
.GalleryItem:focus .HandPick {
  animation: HandPickScale 1s linear infinite;
}
@keyframes HandPickScale {
  0% {
    -ms-transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -ms-transform: scale(0.9);
    -moz-transform: scale(0.9);
    -webkit-transform: scale(0.9);
    -o-transform: scale(0.9);
    transform: scale(0.9);
  }
  100% {
    -ms-transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
}
.GalleryItem:hover .Swords,
.GalleryItem:focus .Swords {
  animation: swordsAnimation 1.5s linear infinite;
}

@keyframes swordsAnimation {
  0% {
    -ms-transform: skew(20deg);
    -moz-transform: skew(20deg);
    -webkit-transform: skew(20deg);
    -o-transform: skew(20deg);
    transform: skew(20deg);
  }
  50% {
    -ms-transform: skew(-20deg);
    -moz-transform: skew(-20deg);
    -webkit-transform: skew(-20deg);
    -o-transform: skew-(-20deg);
    transform: skew(-20deg);
  }
  100% {
    -ms-transform: skew(20deg);
    -moz-transform: skew(20deg);
    -webkit-transform: skew(20deg);
    -o-transform: skew(20deg);
    transform: skew(20deg);
  }
}
